
:root {
  --bar-width: 100%;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*:before {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}
*:after {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 0 1rem;
}

.c-health {
  font-weight: bold;
  font-size: 1.125rem;
}
.c-health:before {
    display: table;
    content: " ";
}
.c-health:after {
    display: table;
    content: " ";
    clear: both;
}
.c-health .c-health--bar {
    position: relative;
    width: 100%;
    color: #272727;
    padding: 0.3125rem 0.625rem;
    margin-top: 0.5rem;
    border: 1px solid #272727;
}
.c-health .c-health--bar:before {
      content: "";
      width: var(--bar-width);
      height: 100%;
      background-color: #54b420;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
}
.c-health .c-health--player {
    float: left;
    width: 20%;
}
.c-health .c-health--dragon {
    float: right;
    width: 20%;
}
.c-health .c-health--dragon .c-health--bar:before {
      background-color: #b42020;
      width: var(--bar-width);
}

.c-controller {
  margin: 3rem auto;
}
.c-controller--btn {
  display: inline-block;
  width: 10%;
  font-weight: bold;
  padding: 0.3125rem 0;
  cursor: pointer;
  margin-left: 0.625rem;
}
.c-controller--btn:first-child {
    margin-left: 0;
}
.c-controller--btn__attack {
  background-color: #ED254E;
  color: white;
}
.c-controller--btn__blast {
  background-color: #F9DC5C;
  color: #272727;
}
.c-controller--btn__heal {
  background-color: #C2EABD;
  color: #272727;
}
.c-controller--btn__surrender {
  background-color: #011936;
  color: white;
}

.c-comment--list[data-v-1b7790e3] {
  list-style: none;
  padding-left: 0;
  margin: 0;
}
.c-comment--list li[data-v-1b7790e3] {
    padding: 0.3125rem;
}
.c-comment--list li[data-v-1b7790e3]:nth-child(odd) {
      background-color: #efefef;
}
.c-comment--list li[data-v-1b7790e3]:nth-child(even) {
      background-color: #dedede;
}
